  //   let usersArray = []
  //   const chain = state.chain
  //   chain.forEach((item)=>{
  //     item.transactions.forEach((transaction)=>{
  //       const signature = transaction.signature
  //       console.log(signature)
  //       if (!usersArray.includes(signature)) {
  //         usersArray.push(signature)
  //       }
  //     })
  //   })
  //   setUsers(usersArray)
