import {useEffect, useState} from "react";

function UsersList(user) {
    // const [state, setState] = useState({
    //     "chain": []
    // })
    const [transactions, setTransactions] = useState([])


    // useEffect(()=>{
    //
    // }, [state])
    const getTransactions = () =>{
        console.log(user.user)
        fetch(`http://localhost:5000/parse_transactions/${user.user}`,)
            .then(response => response.json())
            .then(res => setTransactions(res.transactions))
    }

    // const nullUsers= () =>{
    //     setTransactions(["fdshgbvksujbasedrrrrrrrrrrrrrrrhgbeiogggggggggggggggggggggbyr","2","3"])
    // }

    return (
        <>
            <div onClick={getTransactions}>Стадии</div>
            <table>
                <thead className="u-black u-table-header u-table-header-1">
                <tr>
                    <th className="u-table-cell">Сообщение</th>
                    <th className="u-table-cell">Получатель</th>
                    <th className="u-table-cell">Отправитель</th>
                </tr>
                </thead>
        {transactions.map((transaction)=>{return <tr>
                <td className="u-table-cell">{transaction.message}</td>
                <td className="u-table-cell">{transaction.receiver}</td>
                <td className="u-table-cell">{transaction.sender}</td>
            </tr>
            })}
            </table>
        </>
    );
}

export default UsersList;
